import { asyncRouterMap, constantRouterMap } from '../../router'
import { getRoutes } from '../../api/route'

/**
 * meta.role
 * @param roles
 * @param route
 */
function hasPermission(roles, route) {
  if (route.meta && route.meta.roles) {
    return roles.some(role => route.meta.roles.includes(role))
  } else {
    return true
  }
}

/**
 * 
 * @param routes asyncRouterMap
 * @param roles
 */
function filterAsyncRouter(routes, roles) {
  const res = []

  routes.forEach(route => {
    const tmp = {
      ...route
    }
    if (hasPermission(roles, tmp)) {
      if (tmp.children) {
        tmp.children = filterAsyncRouter(tmp.children, roles)
      }
      res.push(tmp)
    }
  })

  return res
}

function requestRoutes() {
  return getRoutes()
}

function formatRoutes(routes) {
  const fmRoutes = []
  routes.forEach(router => {
    if (router.component) {
      const component = router.component
      router.component = resolve => {
        // 
        // 
        // '@/**/*.vue'
        require(['@/' + component + '.vue'], resolve)
      }
    } else if (router.template) {
      router.component = resolve => {
        resolve({
          template: router.template
        })
      }
    }

    let children = router.children
    if (children && children instanceof Array) {
      children = formatRoutes(children)
    }

    router.children = children

    fmRoutes.push(router)
  })

  return fmRoutes
}

const permission = {
  state: {
    // 
    routers: constantRouterMap,
    // 
    addRouters: []
  },
  mutations: {
    SET_ROUTERS: (state, routers) => {
      state.addRouters = routers
      state.routers = constantRouterMap.concat(routers)
    }
  },
  actions: {
    GenerateRoutes({ commit }, data) {
      return new Promise(async resolve => {
        const { roles } = data
        let accessedRouters
        const routeRes = await requestRoutes()
        const asyncRouters = asyncRouterMap.concat(formatRoutes(routeRes.data))
        if (roles.includes('admin')) {
          accessedRouters = asyncRouters
        } else {
          accessedRouters = filterAsyncRouter(asyncRouters, roles)
        }
        commit('SET_ROUTERS', accessedRouters)
        resolve()
      })
    }
  }
}

export default permission
